.form-container {
  width: 100%;
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.form-container .form {
  padding: .5rem;
}

.form .input-number {
  padding: .5rem;
  border-radius: 5px;
  border: none;
  outline: 2px solid #128c7e;
  transition: all 150ms ease-in-out;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 1.1rem;
  font-weight: 300;
}

.form .input-number:focus {
  outline: 3px solid #128c7e;
}

.form .message {
  font-size: .8rem;
  margin-top: 1rem;
  font-weight: 400;
}

.form .boton {
  display: block;
  width: 150px;
  margin: auto;
  margin-top: 1rem;
  color: #fff;
  padding: .7rem;
  border-radius: 5px;
  text-decoration: none;
  background-color: #128c7e;
}

.form .boton:hover {
  background-color: #0d7e71;
}